:root {
	--yellow: #f8bf66;
	--brown-light: #b2966c;
	--brown-dark: #a27150;
	--green-light: #99b474;
	--green-dark: #628060;
	--gray: #8ea39f;
	--white-background: #fbf3e4;
	--black-typography: #404040;
}

body {
	background-color: var(--white-background);
}

.titolo {
	font-family: "Rubik One", sans-serif;
	font-weight: bold;
	color: var(--black-typography);
}

.sottotitolo {
	font-family: "Now", sans-serif;
	text-transform: uppercase;
	font-weight: bold;
	color: var(--black-typography);
}

.corpo {
	font-family: "Now", sans-serif;
	color: var(--black-typography);
}

.row {
	display: flex;
	align-items: center;
	margin-top: 5px;
	margin-bottom: 5px;
}

.col {
	margin-right: 5px;
	margin-left: 5px;
}
