.title-text {
	margin-left: 5px;
	justify-content: right;
	font-size: 30px;
}

.logo {
	margin: 10px;
	max-width: 120px;
	max-height: auto;
}

.main-container {
	width: 100%;
	display: flex;
}

#map-container {
	overflow: hidden;
}

.map-column {
	flex: 1;
	height: auto;
	padding-left: 10px;
}

.data-column {
	flex: 2;
	padding-right: 10px;
}

.pin {
	position: absolute;
	width: 45px;
	height: 60px;
	top: 50%;
	right: 50%;
}
